import HttpRelay from 'httprelay'
import domReady from '@wordpress/dom-ready'

// Creating a random name server and get proxy communication method instance
let proxy = new HttpRelay(new URL('https://demo.httprelay.io')).proxy()

// Registering a handler for the route
proxy.routes.addGet('/hello', 'hello', () => {
	// This code will be executed in your browser
	return `Hello from your browser: ${navigator.appVersion}!`
})

// Starting server
proxy.start()

// NOT IMPORTANT ///////////////////////////////////////////////////////////////////////////////
domReady(() => {
	document.getElementById('proxyUrl').innerHTML = proxy.proxyUrl.href
	document.getElementById('proxyServer').innerHTML = proxy.serverId
	document.getElementById('proxyPath').innerHTML = proxy.routes.get('hello').path
	document.getElementById('serverLink').setAttribute('href', proxy.routes.get('hello').url().href)
})
